"use client"
import { useAppDispatch } from "@/lib/global/hooks";
import { cusLogin } from "@/lib/slice/auth/cus.auth.slice";
import { IcusLogin } from "@/lib/slice/auth/cus.auth.types";
import { ChangeEvent, FormEvent, useState } from "react";
import { ToastContainer } from "react-toastify";
const Login = () => {
  const dispatch = useAppDispatch()


  const [data, setData] = useState<IcusLogin>({
    email: "",
    password: ""
  })

  const onChangeData = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setData({
      ...data,
      [name]: value
    })

  }

  const onSubmitData = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    dispatch(cusLogin(data))

  }




  return <>
    <ToastContainer />
    <div className="flex flex-row justify-center items-center mt-50 ">
      <form className="bg-gray-100">
        <div className="email mb-6">        <label className="bg-white">Email</label>
          <input onChange={onChangeData} className="ml-5 px-5" type="string" name="email" placeholder="admin@gmail.com" value={data.email} required />
        </div>

        <div className="password ">        <label className="bg-white">Password</label>
          <input onChange={onChangeData} className="ml-5 px-5" type="password" name="password" placeholder="********" value={data.password} required />
        </div>

        <div className="buttom mt-5 flex flex-col justify-center bg-blue-500 border ">
          <button>Login</button>
        </div>

      </form>

    </div>

  </>

}

export default Login
